var isSafari =
  navigator.vendor &&
  navigator.vendor.indexOf('Apple') > -1 &&
  navigator.userAgent &&
  navigator.userAgent.indexOf('CriOS') === -1 &&
  navigator.userAgent.indexOf('FxiOS') === -1;
let deferredPrompt;
let btn = document.querySelector('.prompt');

// For safari, we hide the custom banner since there's no way to
// programmatically trigger this event
if (!isSafari || true) {
  btn.style.display = 'block';
  window.addEventListener('beforeinstallprompt', e => {
    e.preventDefault();
    deferredPrompt = e;
    showPrompt();
  });
}

function showPrompt() {
  btn.classList.toggle('prompt--display', true);
  btn.addEventListener('click', add2HomeScreen);
}

function add2HomeScreen(e) {
  btn.classList.toggle('prompt--display', false);
  deferredPrompt.prompt();
  deferredPrompt.userChoice.then(res => {
    if (res.outcome === 'accept') {
      console.log('accepted');
    } else {
      console.log('denied');
    }
    deferredPrompt = null;
  });
}
