import * as types from './actionTypes';
import { action } from 'typesafe-actions';

export const toggleBookmark = (shopId: number) =>
  action(types.TOGGLE_BOOKMARK, { shopId });

export const addBookmark = (...shopIds: number[]) =>
  action(types.ADD_BOOKMARK, { shopIds });

export const removeBookmark = (...shopIds: number[]) =>
  action(types.REMOVE_BOOKMARK, { shopIds });
