import { ShopDBSchema } from './../types';
import * as shopActions from './actions';
import * as types from './actionTypes';
import { call, put, takeEvery, takeLatest } from 'redux-saga/effects';
import axios from 'axios';
import { getAllStore, insertStore, cleanStore } from '../../localDB';
import { SagaIterator } from 'redux-saga';

const endpoint = `${process.env.REACT_APP_API_HOST}${
  process.env.REACT_APP_SHOPS_ENDPOINT
}`;

// Workaround for correct typings with saga.
// https://github.com/piotrwitek/typesafe-actions#with-redux-saga-sagas

function* dbGet(action: ReturnType<typeof shopActions.dbGet>): SagaIterator {
  try {
    const response = yield call(getAllStore, action.payload);
    yield put(shopActions.dbGetFulfilled(response));
  } catch (error) {
    yield put(
      shopActions.dbGetRejected({
        name: 'DB_ERROR', //TODO: name needed as defined in interface APIError
        message: error.message,
        stack: error.toString(),
      })
    );
  }
}

function* fetchRemote(): SagaIterator {
  try {
    yield call(cleanStore, 'shops');
    const response = yield call(axios.get, endpoint);
    const data: ShopDBSchema[] = response.data;
    // If the store name is all numers (e.g. "1988"), Google Sheets may export
    // it as a number. This will break everything. So we fix it up here.
    for (let d of data) {
      d.name = "" + d.name;
    }
    yield put(shopActions.fetchRemoteFulfilled(response));
    yield put(shopActions.updateDb('shops', data));
  } catch (error) {
    yield put(shopActions.dbGetRejected(error));
  }
}

function* updateDB(
  action: ReturnType<typeof shopActions.updateDb>
): SagaIterator {
  const { storeName, data } = action.payload;
  try {
    yield call(insertStore, storeName, data);
    // Question: should it be dbGet(storeName)
    yield put(shopActions.dbGet('shops'));
  } catch (error) {
    yield put(shopActions.updateDbRejected(error));
  }
}

function* watchDbGet(): SagaIterator {
  yield takeEvery(types.DB_GET, dbGet);
}
function* watchFetchRemote(): SagaIterator {
  yield takeLatest(types.FETCH_REMOTE, fetchRemote);
}
function* watchUpdateDB(): SagaIterator {
  yield takeEvery(types.UPDATE_DB, updateDB);
}

export default [watchDbGet, watchFetchRemote, watchUpdateDB];
