import { AppLatLng } from './../types';
import * as types from './actionTypes';
import { action } from 'typesafe-actions';
import { ShopState } from '../types';

export const setSearchTerm = (
  searchTerm: string,
  center: AppLatLng,
  rangeKM: number
) =>
  action(types.SET_SEARCH_TERM, {
    searchTerm,
    center,
    rangeKM,
  });

export const setSearchResult = (searchResult: ShopState[]) =>
  action(types.SET_SEARCH_RESULT, {
    searchResult,
  });

export const setSelectedShopIndex = (index: number) =>
  action(types.SET_SELECTED_SHOP_INDEX, { index });
