import { combineReducers, Reducer } from 'redux';
import * as types from './actionTypes';
import { toRenderUsage } from './viewControlMapper';
import {
  AppAction,
  ShopState,
  ErrorMessageState,
  ShopDBSchema
} from '../types';

export interface ShopStore {
  content: ShopState[] | [];
  error: ErrorMessageState;
}

// export type ErrorMessageState = string;
// Avoid using type constant here so that reducers are "paired with" actions not constants
const content: Reducer<ShopState[], AppAction> = (state = [], action) => {
  switch (action.type) {
    case types.DB_GET_FULFULLED:
      const shopsDB = action.payload.shops as ShopDBSchema[];
      // this serve as decouple process from DB to rendering
      const shops = shopsDB.map(toRenderUsage);
      return shops;
    default:
      return state;
  }
};

const error: Reducer<ErrorMessageState, AppAction> = (
  state = { message: '', stack: '' },
  action
) => {
  switch (action.type) {
    case types.DB_GET_REJECTED:
    case types.FETCH_REMOTE_REJECTED:
      return action.error;
    default:
      return state;
  }
};

export default combineReducers({
  content,
  error
});
