import * as types from './actionTypes';
import { APIResponse, APIError } from '../types';
import { action } from 'typesafe-actions';
import { InitConfigStore } from './reducers';
const APP_VERSION = process.env.REACT_APP_VERSION || '';
// Bottom Up approach to create actions.
// Allow better auto-complete with type narrowing
// action API:
// (type: T, payload?: P, meta?: M, error?: E): {
//   type: T;
//   payload: P;
//   meta: M;
//   error: E;
// };

// Saga will potentially use payload from here.
// Initiating {} as payload.
export const initFetch = () => action(types.INIT_FETCH, {});

export const initFetchFulFilled = (response: APIResponse) =>
  action(types.INIT_FETCH_FULFILLED, response.data);

export const initFetchRejected = (error: APIError) =>
  action(types.INIT_FETCH_REJECTED, {}, undefined, error);

export const initFetchConfig = () => action(types.INIT_FETCH_CONFIG, {});

export const initFetchConfigFulFilled = (response: APIResponse) =>
  action(types.INIT_FETCH_CONFIG_FULFILLED, response.data);

export const initFetchConfigRejected = (error: APIError) =>
  action(types.INIT_FETCH_CONFIG_REJECTED, {}, undefined, error);

export const checkAppVersion = (store: InitConfigStore) =>
  action(types.CHECK_APP_VERSION, undefined, { store });

export const updateAppVersion = (version: string = APP_VERSION) =>
  action(types.UPDATE_APP_VERSION, { version });
