import { VersionAPISchema } from '../types';
import { VersionState } from './reducers';
export const toRenderUsage = (
  res: VersionState,
  version: VersionAPISchema,
  index: number,
  ctx: VersionAPISchema[]
): VersionState => {
  return index === 0
    ? {
        shoplist: version.shopListVersion,
        client: version.clientVersion,
      }
    : res;
};
