import React, { createContext, FC } from 'react';
import { BrowserRouter, Route, match } from 'react-router-dom';
import { Location, History } from 'history';

export interface UseRouterContext {
  history: History;
  location: Location;
  match: match<any> | null;
}

const initContext = {} as UseRouterContext;

export const RouterContext = createContext(initContext);

const ContextedRouter: FC = props => {
  return (
    <BrowserRouter>
      <Route>
        {routeProps => (
          <RouterContext.Provider value={routeProps}>
            {props.children}
          </RouterContext.Provider>
        )}
      </Route>
    </BrowserRouter>
  );
};

export default ContextedRouter;
