import * as types from './actionTypes';
import { APIError } from '../types';
import { action } from 'typesafe-actions';

// Bottom Up approach to create actions.
// Allow better auto-complete with type narrowing
// action API:
// (type: T, payload?: P, meta?: M, error?: E): {
//   type: T;
//   payload: P;
//   meta: M;
//   error: E;
// };

export const dbGet = (storeName: string) => action(types.DB_GET, storeName);

export const dbGetFulfilled = (response: any) =>
  action(types.DB_GET_FULFULLED, response);

export const dbGetRejected = (error: APIError) =>
  action(types.DB_GET_REJECTED, {}, undefined, error);

export const fetchRemote = () => action(types.FETCH_REMOTE, {});

export const fetchRemoteFulfilled = (response: any) =>
  action(types.FETCH_REMOTE_FULFILLED, response.data);

export const fetchRemoteRejected = (error: APIError) =>
  action(types.FETCH_REMOTE_REJECTED, {}, undefined, error);

export const updateDb = (storeName: string, data: any) =>
  action(types.UPDATE_DB, {
    storeName,
    data
  });

export const updateDbFulfilled = (response: any) =>
  action(types.UPDATE_DB_FULFULLED, response.data);

export const updateDbRejected = (error: APIError) =>
  action(types.UPDATE_DB_REJECTED, {}, undefined, error);
