import { combineReducers, Reducer } from 'redux';
import * as types from './actionTypes';
import { AppAction } from '../types';

export interface BookmarkStore {
  shopIds: number[];
}

export const shopIds: Reducer<number[], AppAction> = (state = [], action) => {
  switch (action.type) {
    case types.TOGGLE_BOOKMARK:
      const shopId = action.payload.shopId;
      if (state.includes(shopId)) {
        return state.filter(id => id !== shopId);
      } else {
        return state.concat([shopId]);
      }

    case types.ADD_BOOKMARK:
      const existing = new Set(state);
      return state.concat(action.payload.shopIds.filter(id => !existing.has(id)));

    case types.REMOVE_BOOKMARK:
      const toRemove = new Set(action.payload.shopIds);
      return state.filter(id => !toRemove.has(id));

    default:
      return state;
  }
};

export default combineReducers({
  shopIds,
});
