import React, { FC } from 'react';
import { RouteComponentProps } from 'react-router';
import PageFrame from 'templates/PageFrame';
import './style.scss';
import { MenuList } from '@material-ui/core';
import { ShopState, ReduxStore } from 'store/types';
import { useSelector } from 'react-redux';
import ShopInfoCard from 'components/ShopInfoCard';

const BookmarkPage: FC<RouteComponentProps> = props => {
  const shops = useSelector((state: ReduxStore) => state.shops.content);
  const idMap = new Map<number, ShopState>();
  for (let shop of shops) {
    idMap.set(shop.id, shop);
  }
  const bookmarkShopIds = useSelector(
    (state: ReduxStore) => state.bookmark.shopIds
  );

  return (
    <PageFrame title="收藏" {...props}>
      <MenuList>
        {bookmarkShopIds.map(id => {
          const shop = idMap.get(id);
          if (!shop) return null;
          return (
            <ShopInfoCard
              key={id}
              shop={shop}
              bookmarked={true}
              refLatLng={null}
            />
          );
        })}
      </MenuList>
    </PageFrame>
  );
};

export default BookmarkPage;
