import React, { FC, useRef } from 'react';
import clsx from 'clsx';
import { useDispatch } from 'react-redux';
import { toggleNavMenu, closeNavMenu } from 'store/NavMenu/actions';
import { InputBase, IconButton, Paper, makeStyles } from '@material-ui/core';
import { Menu as MenuIcon, Search as SearchIcon } from '@material-ui/icons';

const useStyles = makeStyles({
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    position: 'relative',
    borderRadius: '24px',
  },
  form: {
    marginLeft: 8,
    flex: 1,
  },
  input: {
    width: '100%',
  },
  iconButton: {
    padding: 10,
  },
});

type Props = {
  searchTerm: string;
  handleSearchTermChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  handleQuery: () => void;
  className?: string;
};
const SearchBar: FC<Props> = props => {
  const { searchTerm, handleSearchTermChange, handleQuery, className } = props;
  const classes = useStyles();
  const dispatch = useDispatch();
  const searchInputRef = useRef<HTMLInputElement>();

  return (
    <>
      <Paper className={clsx(classes.root, className)}>
        <IconButton aria-label='menu'
          onClick={e => {
            e.preventDefault();
            dispatch(toggleNavMenu());
          }}
        >
          <MenuIcon />
        </IconButton>
        <form className={classes.form} action=".">{/* required for iOS to display a keyboard with "Search" action */}
          <InputBase
            className={classes.input}
            placeholder='搜尋'
            type='search'
            inputProps={{ 'aria-label': 'searchfield' }}
            value={searchTerm}
            inputRef={searchInputRef}
            onClick={() => dispatch(closeNavMenu())}
            onChange={handleSearchTermChange}
            onKeyPress={e => {
              if (e.key === 'Enter') {
                e.preventDefault(); // don't actually submit the form
                if (searchInputRef.current) {
                  searchInputRef.current.blur();
                }
                handleQuery();
              }
            }}
          />
        </form>
        <IconButton
          className={classes.iconButton}
          aria-label='search'
          onClick={handleQuery}
        >
          <SearchIcon />
        </IconButton>
      </Paper>
    </>
  );
};

export default SearchBar;
