import { combineReducers, Reducer } from 'redux';
import * as types from './actionTypes';
import { toRenderUsage } from './viewControlMapper';
import { AppAction } from '../types';

export interface InitConfigStore {
  version: VersionState;
  config: ConfigState;
  error: ErrorMessageState;
  appVersion: string;
}

export interface VersionState {
  shoplist: number;
  client: number;
}
export interface ConfigState {
  reportDataFormUrl: string;
}
export interface ErrorMessageState {
  message: string;
  stack: string;
}

export const initVersion = {
  shoplist: 0,
  client: 0,
};

const version: Reducer<VersionState, AppAction> = (
  state = initVersion,
  action
) => {
  switch (action.type) {
    case types.INIT_FETCH_FULFILLED:
      const version = action.payload.reduce(toRenderUsage, initVersion);
      return version;
    default:
      return state;
  }
};
const config: Reducer<ConfigState, AppAction> = (
  state = { reportDataFormUrl: '' },
  action
) => {
  switch (action.type) {
    case types.INIT_FETCH_CONFIG_FULFILLED:
      return { ...state, ...action.payload[0] };
    default:
      return state;
  }
};
const error: Reducer<ErrorMessageState, AppAction> = (
  state = { message: '', stack: '' },
  action
) => {
  switch (action.type) {
    case types.INIT_FETCH_REJECTED:
    case types.INIT_FETCH_CONFIG_REJECTED:
      return action.error;
    default:
      return state;
  }
};

const appVersion: Reducer<string, AppAction> = (state = '', action) => {
  switch (action.type) {
    case types.UPDATE_APP_VERSION:
      return action.payload.version;
    default:
      return state;
  }
};

export default combineReducers({
  version,
  config,
  error,
  appVersion,
});
