import { ShopDBSchema, ShopDetailSchema } from './../types';
import * as shopDetailActions from './actions';
import * as types from './actionTypes';
import { call, put, takeEvery, takeLatest } from 'redux-saga/effects';
import axios from 'axios';
import { getAllStore, insertStore, cleanStore } from '../../localDB';
import { SagaIterator } from 'redux-saga';

// const endpoint = `${process.env.REACT_APP_API_HOST}${process.env.REACT_APP_SHOPS_ENDPOINT}`;
const getShopDetailUrl = (shopID: number) =>
  `${process.env.REACT_APP_API_HOST}${process.env.REACT_APP_SHOP_DETAIL_FOLDER_ENDPOINT}/${shopID}.json`;

function* fetchShop(
  action: ReturnType<typeof shopDetailActions.fetchShop>
): SagaIterator {
  try {
    const { data } = yield call(axios.get, getShopDetailUrl(action.payload.id));
    yield put(shopDetailActions.fetchShopFulfilled(data));
  } catch (error) {
    // TODO: Dispatch error
  }
}

function* watchFetchShop(): SagaIterator {
  yield takeEvery(types.FETCH_SHOP, fetchShop);
}

export default [watchFetchShop];
