import { createStore, applyMiddleware } from 'redux';
import { reducers, sagas } from './store';
import createSagaMiddleware from 'redux-saga';
import { persistStore, persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import { composeWithDevTools } from 'redux-devtools-extension';

const configStore = () => {
  // saga setup
  const sagaMiddleware = createSagaMiddleware();

  // persist setup
  const persistConfig = {
    key: '__blackmirror',
    storage: storage,
    debug: true,
    blacklist: ['navMenu', 'shops', 'searchState', 'shopDetail'],
    whitelist: ['bookmark', 'initConfig'],
  };

  const persistedReducer = persistReducer(persistConfig, reducers);

  const middlewares = [sagaMiddleware];

  const configStore =
    process.env.NODE_ENV === 'production'
      ? createStore(persistedReducer, applyMiddleware(...middlewares))
      : createStore(
          persistedReducer,
          composeWithDevTools(applyMiddleware(...middlewares))
        );

  sagas.forEach(saga => {
    sagaMiddleware.run(saga);
  });

  const persistor = persistStore(configStore);

  return { store: configStore, persistor };
};

export default configStore;
