import React, { FC } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CssBaseline from '@material-ui/core/CssBaseline';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import './style.scss';
import { IconButton, Icon, makeStyles, Slide, AppBar } from '@material-ui/core';
import Navigation from 'components/Navigation';

export interface PageFrameProps extends RouteComponentProps {
  title: string;
}

const useStyles = makeStyles(theme => ({
  iconButton: {
    fontSize: '20px',
    color: '#000',
    '&:hover': {
      backgroundColor: '#FFF',
    },
  },
  toolbar: {
    position: 'fixed',
    top: 0,
    width: '100%',
    backgroundColor: '#FFF',
    color: '#000',
    borderBlockEnd: '1px solid #aaa',
    zIndex: theme.zIndex.appBar,
  },
  title: {
    width: '100%',
    textAlign: 'center',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
  },
}));

const PageFrame: FC<PageFrameProps> = props => {
  const { children, title, ...router } = props;
  const classes = useStyles();
  return (
    <Slide direction='left' in mountOnEnter unmountOnExit>
      <div>
        <CssBaseline />
        <Navigation {...props} />
        <Toolbar className={classes.toolbar}>
          <IconButton
            onClick={() => router.history.push('/')}
            edge='start'
            className={classes.iconButton}
          >
            <i className='icon-back' />
          </IconButton>
          <Typography className={classes.title} variant='h6'>
            {title}
          </Typography>
          <Box style={{ width: '48px' }} />
        </Toolbar>
        <Container>
          <Toolbar />
          <Box my={2}>{children}</Box>
        </Container>
      </div>
    </Slide>
  );
};

export default PageFrame;
