// get request object from window or self (service worker)
var name = process.env.REACT_APP_DB_NAME;
var storeName = process.env.REACT_APP_DB_STORE;
var db = null;

export const openDB = version => {
  const indexedDB = window.indexedDB;
  if (!indexedDB) throw new Error('IndexedDB is not supported');
  return new Promise((resolve, reject) => {
    if (db !== null) {
      resolve(db);
    }
    const request = indexedDB.open(name, version);
    request.onsuccess = event => {
      console.log('[IndexedDB][success]', event.target.result);
      resolve(event.target.result);
    };
    request.onerror = event => {
      console.log('[IndexedDB][onerror]', request.error);
      reject(event);
    };
    request.onupgradeneeded = event => {
      console.log('[IndexedDB][upgradeneeded]');
      event.target.result.createObjectStore(storeName, {
        autoIncrement: true,
        keyPath: 'id',
      });
    };
  });
};

export const insertStore = async (storeName, dataArray) => {
  const localDB = await openDB();
  const transaction = localDB.transaction(storeName, 'readwrite');
  return new Promise((resolve, reject) => {
    const store = transaction.objectStore(storeName);
    dataArray.forEach(data => {
      const request = store.add(data);
      request.onerror = event => {
        transaction.abort();
      };
    });
    transaction.oncomplete = event => {
      console.log('[indexDB][transaction][insert]', 'success', event);
      resolve(event.target.result);
    };
    transaction.onerror = event => {
      console.log('[indexDB][transaction][insert]', 'aborted', event);
      reject(event.error);
    };
    transaction.onabort = event => {
      console.log('[indexDB][transaction][insert]', 'aborted', event);
      reject(event.error);
    };
    console.log('transaction?', transaction);
  });
};

export const getAllStore = async storeName => {
  const localDB = await openDB();
  const transaction = localDB.transaction(storeName, 'readonly');
  return new Promise((resolve, reject) => {
    const store = transaction.objectStore(storeName);
    const request = store.getAll();
    request.onsuccess = event => {
      console.log('[indexDB][transaction][GET ALL]', 'success', event);
      resolve({
        [storeName]: event.target.result,
      });
    };
    request.onerror = event => {
      console.log('[indexDB][transaction][GET ALL]', 'failed', event);
      transaction.abort();
      reject(event.error);
    };
  });
};

export const cleanStore = async storeName => {
  const localDB = await openDB();
  const transaction = localDB.transaction(storeName, 'readwrite');
  return new Promise((resolve, reject) => {
    const store = transaction.objectStore(storeName);
    const request = store.clear();
    request.onsuccess = event => {
      console.log('[indexDB][transaction][Clear All]', 'success', event);
      resolve(event.target.result);
    };
    request.onerror = event => {
      console.log('[indexDB][transaction][Clear All]', 'failed', event);
      reject(event.error);
    };
  });
};
