import * as types from './actionTypes';
import * as initConfigActions from './actions';
import * as shopActions from '../Shop/actions';
import { call, put, takeLatest, select } from 'redux-saga/effects';
import { getAllStore, insertStore, cleanStore } from '../../localDB';
import { forceUpdateApp } from 'helpers';
import axios from 'axios';
import { toRenderUsage } from './viewControlMapper';
import { SagaIterator } from 'redux-saga';
import { initVersion, VersionState, ConfigState } from './reducers';

const APP_VERSION = process.env.REACT_APP_VERSION || '';

const versionEndpoint = `${process.env.REACT_APP_API_HOST}${process.env.REACT_APP_INIT_ENDPOINT}`;

const configEndpoint = `${process.env.REACT_APP_API_HOST}${process.env.REACT_APP_INIT_CONFIG_ENDPOINT}`;
// Workaround for correct typings with saga.
// https://github.com/piotrwitek/typesafe-actions#with-redux-saga-sagas

function* fetchInit(
  action: ReturnType<typeof initConfigActions.initFetch>
): SagaIterator {
  try {
    const response = yield call(axios.get, versionEndpoint, action.payload);
    const version: VersionState = response.data.reduce(
      toRenderUsage,
      initVersion
    );
    const currentVersion: VersionState = yield select(
      state => state.initConfig.version
    );
    version.shoplist > currentVersion.shoplist
      ? yield put(shopActions.fetchRemote())
      : yield put(shopActions.dbGet('shops'));
    yield put(initConfigActions.initFetchFulFilled(response));
  } catch (error) {
    yield put(initConfigActions.initFetchRejected(error));
  }
}
function* fetchInitConfig(
  action: ReturnType<typeof initConfigActions.initFetchConfig>
): SagaIterator {
  try {
    const response = yield call(axios.get, configEndpoint);
    yield put(initConfigActions.initFetchConfigFulFilled(response));
  } catch (error) {
    yield put(initConfigActions.initFetchConfigRejected(error));
  }
}

function* checkAppVersion(
  action: ReturnType<typeof initConfigActions.checkAppVersion>
): SagaIterator {
  try {
    const { appVersion } = action.meta.store;
    const currentVersions = APP_VERSION;
    if (appVersion !== currentVersions) {
      // force update
      yield put(initConfigActions.updateAppVersion());
      yield call(forceUpdateApp);
      yield call(window.location.reload, true);
    }
  } catch (error) {}
}

function* watchFetchInit(): SagaIterator {
  yield takeLatest(types.INIT_FETCH, fetchInit);
}
function* watchFetchInitConfig(): SagaIterator {
  yield takeLatest(types.INIT_FETCH_CONFIG, fetchInitConfig);
}
function* watchCheckAppVersion(): SagaIterator {
  yield takeLatest(types.CHECK_APP_VERSION, checkAppVersion);
}

export default [watchFetchInit, watchFetchInitConfig, watchCheckAppVersion];
