import React, { FC, RefObject } from 'react';
import { Map, TileLayer, Marker } from 'react-leaflet';
import { closeNavMenu } from 'store/NavMenu/actions';
import { useDispatch } from 'react-redux';
import { iconMyLocation, iconGrey, iconYellow, iconBlue } from './assets';
import { AppLatLng, ShopState } from 'store/types';

type Props = {
  shops: ShopState[];
  selectedShopIndex: number;
  onPinPress: (index: number) => void;
  userLatLng: AppLatLng | null;
  position: {
    center: AppLatLng;
    zoom: number;
  };
  setPosition: React.Dispatch<
    React.SetStateAction<{
      center: AppLatLng;
      zoom: number;
    }>
  >;
  onMoved: () => void;
  mapRef: RefObject<any>;
};

const MAP_MAX_ZOOM = parseInt(process.env.REACT_APP_MAP_MAX_ZOOM || '20');
const MAP_MIN_ZOOM = parseInt(process.env.REACT_APP_MAP_MIN_ZOOM || '0');
const MAP_SW_MAX = (process.env.REACT_APP_MAP_SW_MAX || '')
  .split(',')
  .map(latlng => parseFloat(latlng));
const MAP_NE_MAX = (process.env.REACT_APP_MAP_NE_MAX || '')
  .split(',')
  .map(latlng => parseFloat(latlng));

const MapComponent: FC<Props> = props => {
  const {
    shops,
    selectedShopIndex,
    onPinPress,
    mapRef,
    onMoved,
    position,
    setPosition,
    userLatLng,
  } = props;
  const dispatch = useDispatch();
  return (
    <Map
      ref={mapRef}
      maxBoundsViscosity={1}
      maxBounds={[MAP_SW_MAX, MAP_NE_MAX] as [number, number][]}
      center={position.center}
      zoom={position.zoom}
      zoomControl={false}
      minZoom={MAP_MIN_ZOOM}
      maxZoom={MAP_MAX_ZOOM}
      onViewportChanged={v => {
        if (v.center && v.zoom) {
          setPosition({
            center: { lat: v.center[0], lng: v.center[1] },
            zoom: v.zoom,
          });
          onMoved();
        }
      }}
      onclick={() => {
        dispatch(closeNavMenu());
      }}
      style={{
        width: '100vw',
        height: '100vh',
      }}
    >
      <TileLayer
        attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        url='https://{s}-tiles.locationiq.com/v2/obk/r/{z}/{x}/{y}.png?key=5e62082062ad0b'
      />
      {userLatLng && (
        <Marker position={userLatLng} icon={iconMyLocation} zIndexOffset={1000}>
          {/* <Popup>Here</Popup> */}
        </Marker>
      )}

      {shops.map((shop, index) => {
        const { id, latlng, rating } = shop;
        const selected = selectedShopIndex === index;
        let icons = iconGrey;
        if (rating < 0.45) {
          icons = iconYellow;
        } else if (rating > 0.55) {
          icons = iconBlue;
        }
        return (
          <Marker
            key={id}
            position={latlng}
            icon={icons[selected ? 1 : 0]}
            onclick={() => onPinPress(index)}
          />
        );
      })}
    </Map>
  );
};

export default MapComponent;
