import { AppLatLng } from './store/types';
import moment from 'moment';

function deg2rad(deg: number) {
  return deg * (Math.PI / 180);
}

function sqr(n: number) {
  return n * n;
}

export function distance(latlng1: AppLatLng, latlng2: AppLatLng) {
  const R = 6371; // Radius of the earth in km
  const dLat = deg2rad(latlng2.lat - latlng1.lat);
  const dLon = deg2rad(latlng2.lng - latlng1.lng);
  const a =
    sqr(Math.sin(dLat / 2)) +
    Math.cos(deg2rad(latlng1.lat)) *
      Math.cos(deg2rad(latlng2.lat)) *
      sqr(Math.sin(dLon / 2));
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  return R * c;
}

const weekDayString = (weekDay: number) =>
  moment()
    .isoWeekday(weekDay)
    .format('ddd');

const parseFormatedTimeRange = (time: string) => {
  const [fromTime, toTime] = time.split('-');
  return `${moment(fromTime, 'HH:mm').format('hh:mma')} - ${moment(
    toTime,
    'HH:mm'
  ).format('hh:mma')}`;
};

export const checkIsOpening = (openingHours: string[]) => {
  const current = moment();
  const currentIsoWeekday = current.isoWeekday();
  const strIncluded = openingHours.find(openingHour => {
    // ["1,7>07:00-21:30"]
    return (
      parseInt(openingHour[0]) <= currentIsoWeekday &&
      parseInt(openingHour[2]) >= currentIsoWeekday
    );
  });
  const [_, times] = (strIncluded || '').split('>');
  if (!times) {
    return false;
  } else {
    return times.split('|').reduce((res, time) => {
      const [fromTime, toTime] = time.split('-');
      return (
        res ||
        (moment(fromTime, 'HH:mm').isBefore(current, 'minute') &&
          moment(toTime, 'HH:mm').isAfter(current, 'minute'))
      );
    }, false);
  }
};

export const forceUpdateApp = async () => {
  if ('serviceWorker' in navigator) {
    try {
      const registrations = await navigator.serviceWorker.getRegistrations();
      for (let registration of registrations) {
        registration.update();
      }
    } catch (error) {
      console.log(error);
    }
  }
};
