import { AppAction } from '../types';
import { Reducer } from 'redux';
import * as types from './actionTypes';

export interface NavMenuState {
  isOpen: boolean;
}

const navMenuReducer: Reducer<NavMenuState, AppAction> = (
  state = { isOpen: false },
  action
) =>
  action.type === types.NAV_MENU_TOGGLE
    ? { isOpen: !state.isOpen }
    : action.type === types.NAV_MENU_CLOSE
    ? { isOpen: false }
    : state;

export default navMenuReducer;
