import React, { FC, Suspense, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
// Icons
import CommentIcon from '@material-ui/icons/Comment';
import LinkIcon from '@material-ui/icons/Link';
import PlaceIcon from '@material-ui/icons/Place';
import ClockIcon from '@material-ui/icons/AccessTime';
// Icons for action List
import MapIcon from '@material-ui/icons/Map';
import CallIcon from '@material-ui/icons/Call';
import ShareIcon from '@material-ui/icons/Share';
import ReportIcon from '@material-ui/icons/RecordVoiceOver';
import BookmarkIcon from '@material-ui/icons/Bookmark';
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';
// Icons for industry type
import IndustryCateringIcon from '@material-ui/icons/Restaurant';
import IndustryRetailIcon from '@material-ui/icons/StoreMallDirectory';
import IndustryPersonIcon from '@material-ui/icons/Person';
import IndustryCompanyIcon from '@material-ui/icons/Business';

import { ReduxStore, ShopState, ShopDetailSchema } from '../../store/types';
import PageFrame from 'templates/PageFrame';
import { RouteComponentProps } from 'react-router';
import { useRouter } from 'router';
import LoadingSpinner from 'components/LoadingSpinner';
import { checkIsOpening } from 'helpers';
import { MenuList, MenuItem } from '@material-ui/core';
import * as bookmarkActions from 'store/Bookmark/actions';
import * as shopDetailActions from 'store/ShopDetail/actions';

const iconForIndustry = [
  <IndustryCateringIcon />,
  <IndustryRetailIcon />,
  <IndustryPersonIcon />,
  <IndustryCompanyIcon />,
];

const useStyles = makeStyles(theme => ({
  shopDetailRow: {
    display: 'flex',
    padding: theme.spacing(1),
  },
  shopDetailRowIcon: {
    marginRight: theme.spacing(1),
  },
  openHoursList: {
    flex: '1 0 100%',
    paddingTop: '0',
    paddingBottom: '0',
  },
  openHoursItem: {
    flex: '1 0 100%',
    flexFlow: 'row nowrap',
    justifyContent: 'space-between',
    paddingTop: '0',
    paddingBottom: '0',
    minHeight: '24px',
  },
  proofPhotoCard: {
    height: 200,
    width: 200,
    flexShrink: 0,
    marginRight: theme.spacing(1),
  },
  proofPhotoCardMedia: {
    height: '100%',
    width: '100%',
  },
  proofLink: {
    wordBreak: 'break-all',
  },
}));

type DetailPageProps = {
  shopID: number;
};

const DetailPage: FC<
  DetailPageProps & RouteComponentProps<{ shopID: string }> // use router matching
> = props => {
  // Styles
  const classes = useStyles();

  // Use params to simplify getting shop ID
  const {
    match: { params },
  } = props;

  const shopID = parseInt(params.shopID);
  console.log('ShopID from route', shopID);

  // DIspatch
  const dispatch = useDispatch();

  // TODO:
  // 2. Move fetching shop detail to redux / saga
  // WIP: Fetch shop detail,
  // const { shop, loading } = useFetchShopDetail(shopID);
  const details = useSelector((state: ReduxStore) => state.shopDetail);
  console.log('details', details);
  const shopDetail: ShopDetailSchema | undefined = useSelector(
    (state: ReduxStore) => state.shopDetail[shopID]
  );
  useEffect(() => {
    if (!shopDetail) {
      dispatch(shopDetailActions.fetchShop(shopID));
      console.log('dispatched fetchshop');
    }
  }, [shopDetail, dispatch, shopID]);

  const reportDataFormUrl = useSelector((state: ReduxStore) => {
    if (!shopDetail) return '';
    const { id, name, address } = shopDetail;
    const { reportDataFormUrl: url } = state.initConfig.config;
    return url
      .replace('{id}', `${id}`)
      .replace('{name}', name)
      .replace('{address}', address);
  });

  // Shop brief info from store
  const shop =
    useSelector((state: ReduxStore) =>
      state.shops.content.find(shop => shop.id === shopID)
    ) || ({} as ShopState);

  const isInBookmark = useSelector((state: ReduxStore) =>
    state.bookmark.shopIds.includes(shopID)
  );

  return (
    <PageFrame title={shop.name} {...props}>
      <Suspense fallback={LoadingSpinner}>
        <Box display='flex' alignItems='center' p={1}>
          {iconForIndustry[shop.industryType - 1]} {shop.industryTypeName}
        </Box>
        <Divider />
        {!shopDetail && <LoadingSpinner />}
        {shopDetail && (
          <>
            <Box display='flex' justifyContent='space-evenly' p={2}>
              {[
                {
                  enabled: true,
                  icon: <MapIcon />,
                  label: 'Route',
                  onClick: () => {
                    window.location.href = encodeURI(
                      `https://www.google.com/maps/dir/?api=1&destination=${shop.latlng.lat},${shop.latlng.lng}`
                    );
                  },
                },
                {
                  enabled: shopDetail.phoneNo.length > 0,
                  icon: <CallIcon />,
                  label: 'Call',
                  onClick: () => {
                    window.location.href = encodeURI(
                      `tel:${shopDetail.phoneNo}`
                    );
                  },
                },
                {
                  /* navigator.share is present only when the page is served over HTTPS */
                  enabled: navigator.hasOwnProperty('share'),
                  icon: <ShareIcon />,
                  label: 'Share',
                  onClick: () => {
                    // @ts-ignore
                    navigator.share({
                      title: `自由篩｜${shop.name}`,
                      url: encodeURI(document.location.href),
                    });
                  },
                },
                {
                  enabled: true,
                  icon: <ReportIcon />,
                  label: 'Report',
                  onClick: () => window.open(encodeURI(reportDataFormUrl)),
                },
                {
                  enabled: true,
                  icon: isInBookmark ? (
                    <BookmarkIcon />
                  ) : (
                    <BookmarkBorderIcon />
                  ),
                  label: 'Bookmark',
                  onClick: () =>
                    dispatch(bookmarkActions.toggleBookmark(shopDetail.id)),
                },
              ].map(
                ({ enabled, icon, label, onClick }, index) =>
                  enabled && (
                    <Box
                      key={index}
                      display='flex'
                      flexDirection='column'
                      alignItems='center'
                    >
                      <IconButton onClick={onClick}>{icon}</IconButton>
                      {label}
                    </Box>
                  )
              )}
            </Box>
            <Divider />
            <Box className={classes.shopDetailRow}>
              <PlaceIcon className={classes.shopDetailRowIcon} />
              <Box>{shopDetail.address}</Box>
            </Box>
            <Divider />
            <Box className={classes.shopDetailRow}>
              <ClockIcon className={classes.shopDetailRowIcon} />
              <Box display='flex' flexDirection='column'>
                {/*
                <MenuItem style={{ minHeight: '12px' }}>
                  <div
                    style={{
                      background: checkIsOpening(shop.openingHourList)
                        ? 'green'
                        : 'red',
                      color: '#fff',
                      padding: '2px 10px',
                      borderRadius: '8px'
                    }}
                  >
                    {checkIsOpening(shop.openingHourList) ? 'open' : 'closed'}
                  </div>
                </MenuItem>
                */}
                {shop.openingHourList.map((openHour, i) => (
                  <Box key={`op-${i}`}>{openHour}</Box>
                ))}
              </Box>
            </Box>
            <Divider />
            {shopDetail.proofPhotoLinkList.length > 0 && (
              <Box overflow='scroll'>
                <Box
                  display='inline-flex'
                  flexWrap='nowrap'
                  alignItems='center'
                  p={2}
                >
                  {shopDetail.proofPhotoLinkList.map((link, index) => (
                    <Card className={classes.proofPhotoCard} key={index}>
                      <CardMedia
                        className={classes.proofPhotoCardMedia}
                        component='img'
                        image={link}
                      />
                    </Card>
                  ))}
                </Box>
              </Box>
            )}
            <Divider />
            <Box className={classes.shopDetailRow}>
              <CommentIcon className={classes.shopDetailRowIcon} />
              <Box>
                {shopDetail.commentList.length > 0 ? (
                  shopDetail.commentList.map((comment, index) => (
                    <Box key={index}>{comment}</Box>
                  ))
                ) : (
                  <Box>未有黃/藍回報資料</Box>
                )}
              </Box>
            </Box>
            <Divider />
            <Box className={classes.shopDetailRow}>
              <LinkIcon className={classes.shopDetailRowIcon} />
              <Box display='flex' flexDirection='column'>
                {shopDetail.proofEvidenceLinkList.length > 0 ? (
                  shopDetail.proofEvidenceLinkList.map((link, index) => (
                    <Link
                      key={index}
                      className={classes.proofLink}
                      href={link}
                      target='_blank'
                      rel='noopener'
                    >
                      {shopDetail.proofEvidenceLinkTitleList[index]}
                    </Link>
                  ))
                ) : (
                  <Box>未有黃/藍回報資料</Box>
                )}
              </Box>
            </Box>
          </>
        )}
      </Suspense>
    </PageFrame>
  );
};

export default DetailPage;
