import * as types from './actionTypes';
import { APIError, ShopState, ShopDetailSchema } from '../types';
import { action } from 'typesafe-actions';

export const fetchShop = (id: number) => action(types.FETCH_SHOP, { id });
export const fetchShopFulfilled = (shopDetail: ShopDetailSchema) =>
  action(types.FETCH_SHOP_FULFILLED, { shopDetail });
