import { combineReducers } from 'redux';
import { ReduxStore } from './types';
// import reducers in store
import initConfigReducers from './InitConfig/reducers';
import navMenuReducers from './NavMenu/reducers';
import shopReducers from './Shop/reducers';
import searchReducers from './Search/reducers';
import bookmarkReducers from './Bookmark/reducers';
import shopDetailReducer from './ShopDetail/reducers';
// import sagas in store
import initConfigSaga from './InitConfig/sagas';
import shopSaga from './Shop/sagas';
import searchSaga from './Search/sagas';
import shopDetailSaga from './ShopDetail/sagas';

/**
 *
 */
export const reducers = combineReducers<ReduxStore>({
  initConfig: initConfigReducers,
  navMenu: navMenuReducers,
  shops: shopReducers,
  searchState: searchReducers,
  bookmark: bookmarkReducers,
  shopDetail: shopDetailReducer
});

export const sagas = [
  ...initConfigSaga,
  ...shopSaga,
  ...searchSaga,
  ...shopDetailSaga
];
